
body {
  background-color: #080518;
}
#app {
  font-family: 'Open Sans', sans-serif;
  font-size: 13px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'PT Sans Caption', 'Open Sans', serif;
  font-weight: bold;
}
.aes-button {
  background: transparent;
  border-radius: 20px;
  padding: 5px 15px;
}
.aes-button:hover {
  text-decoration: none;
}
.aes-button--empty {
  border: 1px solid rgba(255, 255, 255, 0.3);
}
.aes-button--empty:hover {
  border-color: rgba(255, 255, 255, 0.9);
}
.footer {
  background-color: transparent;
  color: rgba(255, 255, 255, 0.5);
  padding: 30px;
  font-size: 11px;
}
.footer p {
  margin: 0;
  padding: 0 0 0 30px;
}
.footer a {
  color: rgba(255, 255, 255, 0.5);
  text-decoration: underline;
}



