
.jumbo-local {
  background-color: #fff;
  min-height: 650px;
  text-align: center;
  font-size: 18px;
}
.jumbo-local .container {
  padding: 80px 0;
}
.jumbo-local h1 {
  font-size: 60px;
  line-height: 60px;
  font-weight: 700;
}
.jumbo-local h1--condensed {
  width: 600px;
}
.jumbo-local p {
  padding: 20px 15%;
}
.jumbo-local--inverse {
  background-color: #000;
  color: #fff;
}
.jumbo-local--sided > .container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  text-align: left;
}
.jumbo-local--sided > .container h1 {
  margin-bottom: 30px;
  margin-left: 10px;
}
.jumbo-local--sided > .container p {
  padding: 0 !important;
  margin-left: 10px;
}
.jumbo-local--left {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 500px;
          flex: 0 0 500px;
}
.jumbo-local--right {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}



