
.jumbo {
  background-color: #eee;
  min-height: 650px;
}
.jumbo .container {
  padding: 150px 30px;
}
.jumbo__header {
  font-size: 80px;
  line-height: 80px;
  font-weight: 700;
  background: #362f51;
  /* Old browsers */
  /* FF3.6-15 */
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to right, #362f51 0%, #bd081c 20%, #d28d27 39%, #c51812 59%, #343052 79%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#362f51', endColorstr='#343052', GradientType=1);
  /* IE6-9 */
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.jumbo__header--condensed {
  width: 600px;
}
.jumbo__description {
  font-size: 28px;
}
.jumbo__announcement {
  width: 700px;
  margin-top: 40px;
  line-height: 26px;
  padding: 3px 12px 3px 5px;
  border-radius: 15px;
  color: #fff;
  cursor: pointer;
  text-decoration: none;
  background: rgba(0, 0, 161, 0.05);
  font-size: 12px;
  z-index: 20;
}
.jumbo__announcement .new-pill {
  color: #fff;
  margin-left: 10px;
  padding: 0 6px;
  font-size: 10px;
  line-height: 16px;
  font-weight: 600;
  background: #24b47e;
  text-transform: uppercase;
  border-radius: 10px;
}
.jumbo__announcement .message {
  margin: 0 7px 0 6px;
}
.jumbo__announcement:hover {
  text-decoration: none;
  background: rgba(255, 255, 255, 0.1);
}
.jumbo--inverse {
  background-color: #080518;
  color: #fff;
  background: url('/static/jumbo/pulse-static.png') no-repeat;
  background-position: 70% 50%;
}



