
.callline {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  margin-bottom: 150px;
}
.callline a {
  display: block;
  color: rgba(255, 255, 255, 0.5);
  cursor: pointer;
  -webkit-box-flex: 1;
      -ms-flex: 1 1 33%;
          flex: 1 1 33%;
  padding: 20px;
  border-radius: 5px;
  border: 1px solid transparent;
  font-size: 12px;
  transition: all 0.2s ease-in-out;
}
.callline a:hover {
  text-decoration: none;
  background-color: rgba(255, 255, 255, 0.04);
  border-color: rgba(255, 255, 255, 0.1);
}
.callline a h2 {
  color: #ffffff;
  font-weight: 600;
  font-size: 18px;
  line-height: 18px;
  margin: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.callline a h2 img {
  margin-right: 20px;
}
.callline a p {
  margin: 5px 20px 5px 70px;
}



