
.flag {
  background: url('/static/about/licenses/bg2.jpg') no-repeat;
  background-size: 100% auto;
  background-color: #fff;
}
.flag .jumbo-local {
  background-color: transparent;
}
.licenses {
  background-color: transparent;
  color: #000;
  padding-bottom: 100px;
}
.licenses .container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}
.licenses .container div {
  padding: 0 15px;
  -webkit-box-flex: 1;
      -ms-flex: 1 1 33%;
          flex: 1 1 33%;
}
.licenses .container h2 {
  font-weight: 600;
  font-size: 18px;
  margin: 0;
  height: 60px;
  overflow: hidden;
}
.licenses .container p {
  margin: 10px 20px 5px 0;
  display: block;
  height: 100px;
  overflow: hidden;
}
.licenses .container img {
  border: 1px solid #cccbcb;
  border-radius: 5px;
  padding: 10px;
}
.jumbo--hint {
  font-size: 12px;
  margin-top: 20px;
  padding-top: 15px;
  width: 60%;
  margin-left: 20%;
}
.jumbo--hint img {
  border-bottom: 1px solid #000;
  margin-bottom: 20px;
}
.jumbo--hint p {
  display: block;
}



